//依赖的弹窗模块
import sanshuiModal from '@/components/sanshui-modal'
export default {
	components: {
		sanshuiModal
	},
	data() {
		return {
			activeInput: 0, //当前输入的下标
			digitalList: ['1', '2', '3', '4', '5', '6', '7', '8', '9', '', '0', '-1'], //键盘
			paymentPwd: '', //支付密码内容
			dkFlag: true, //支付密码键盘的显示与隐藏
			current: 0,
			paymentPwds: [], //上一次支付密码内容,历史支付密码,
			msgText: '',
			u50: uni.upx2px(50)
		};
	},
	props: {
		value: {
			type: String,
			default: ''
		},
		digits: {
			type: [Number, String],
			default: 6
		},
		forget: {
			type: Boolean,
			default: true
		},
		/*
		 *	可选值说明（这一个顺序最好不要改，跟当前步骤步进行有关系，自动提交要用到mode跟current的判断  是否要自动提交）
		 *	1 支付密码校验（用于校验安全）
		 *	2 设置支付密码（2步  输入=》确认输入）
		 *	3 修改支付密码（3步 原支付密码=》输入=》确认输入）
		 *	4 找回登录密码(同mode = 1一样，就怕后端校验接口不一样而额外添加的一个mode)
		 *	5 重置支付密码
		 */
		mode: {
			type: [Number, String],
			default: 1
		},
		/*
		 *	可选值说明
		 *	hand 手动
		 *	auto 自动
		 */
		trigger: {
			type: String,
			default: 'auto'
		},
		userName: {
			type: String,
			default: ''
		}
	},
	computed: {
		payPassWord() {
			var payPassWord = this.paymentPwd.split('') || [];
			payPassWord.fill('*');
			return payPassWord;
		},
		_forget() {
			return String(this.forget) === 'false' ? false : true;
		},
		_digits() {
			return isNaN(this.digits) ? 6 : parseInt(this.digits);
		},
		_mode() {
			if (isNaN(this.mode) || !this.mode) {
				return 1;
			}
			return +this.mode
		},
		_titleObj() {
			let arr = [{
					title: '输入支付密码',
					subTitle: '请输入支付密码'
				},
				{
					title: '设置支付密码',
					subTitle: '请设置支付密码',
					twoSubTitle: '请再次输入支付密码以确认'
				},
				{
					title: '修改支付密码',
					twoTitle: '设置支付密码',
					threeTitle: '设置支付密码',
					subTitle: '请输入支付密码 以验证身份',
					twoSubTitle: '请设置新的支付密码',
					threeSubTitle: '请再次输入支付密码以确认',
				},
				{
					title: '输入支付密码',
					subTitle: '请输入支付密码'
				},
				{
					title: '重置支付密码',
					subTitle: '请输入支付密码',
					twoSubTitle: '请再次输入支付密码以确认'
				}
			]
			return arr[this._mode - 1];
		},
		_parentMode() {
			return this._mode === 1 || this._mode === 4 ? 'full' : 'cover';
		},
		_parentPosition() {
			return this._mode === 1 || this._mode === 4 ? 'bottom' : 'middle';
		},
		showPrevText() {
			return this.current >= (this._mode - 1) || (this._mode === 5 && this.current > 0) ? true : false;
		}
	},
	methods: {
		// 模态框显示或者关闭的回调
		changeModal(e) { // 返回是一个Boolean值
			if (e) {
				this.current = 0;
				this.changeKeyboard(); //默认拉起键盘
			} else {
				this.changeKeyboard(false); //默认拉起键盘
				this.clearAll();
			}
		},
		// 开关键盘
		changeKeyboard(flag = true) {
			this.dkFlag = flag;
			!flag && (this.activeInput = -1);
		},
		/*
			pro可选址
			show	显示
			hide	隐藏
			toggle	动态判断
		*/
		modalFun(pro) {
			this.$refs.ssModal[pro]();
		},
		clear() {
			this.paymentPwd = '';
			this.activeInput = 0;
			this.paymentPwds.splice(this.current, 1);
		},
		clearAll() {
			this.paymentPwd = '';
			this.activeInput = 0;
			this.current = 0;
			this.paymentPwds = [];
		},
		showError(msg) {
			if (!msg) return this.msgText = '';
			if (this._mode !== 1 && this._mode !== 4) {
				this.msgText = msg;
			} else {
				uni.showToast({
					title: msg
				})
			}
		},
		forgetPwd() {
			uni.navigateTo({
				url: '/pages/member/changePwd'
			});
		},
		cancel() {
			this.$emit('cancel');
		},
		close() {
			let tips = '';
			if (this._mode === 2) {
				tips = '您是否要放弃设置支付密码';
			} else if (this._mode === 5) {
				tips = '您是否要放弃重置支付密码';
			} else {
				tips = '您是否要放弃修改支付密码';
			}
			uni.showModal({
				title: '关闭提示',
				content: tips,
				confirmText: '我再想想',
				cancelText: '关闭',
				success: (res) => {
					if (!res.confirm) {
						this.modalFun('hide');
					}
				}
			})
		},
		submit(clickFlag) { //clickFlag主动点击
			if (this.trigger !== 'auto' && !clickFlag) return;
			if (this.paymentPwd.length !== this._digits) {
				this.showError('请输入' + this._digits + '位支付密码');
			} else {
				this.showError('');
				if (this._mode === 5 || this._mode === 4 || this.current >= (this._mode - 1)) {
					if (this._mode === 5 && this.current === 0) { //下一步，再次输入
						return this.changeSwiper(1);
					}
					if (this.current > 0) { //需要校验2次支付密码的是否相等
						if (this.paymentPwd !== this.paymentPwds[this.current - 1]) {
							this.showError('两次支付密码输入不一致');
							setTimeout(() => {
								this.paymentPwd = '';
								this.paymentPwds[this.current] = '';
								this.activeInput = 0;
							}, 300)
							return;
						}
						this.showError('');
						if (this._mode === 2) { //设置支付密码
							uni.showLoading({
								title: '正在设置密码'
							});
							// 如果不把逻辑卸载这里面，直接把setTimeout删除即可
							setTimeout(() => { //模拟请求，把这个换成ajax请求哈，或者emit出去 外面去请求，但是建议都集成在这边 省的写一堆重置代码
								uni.hideLoading();
								let response = { //模拟返回
									code: 1,
									value: this.paymentPwd
								}
								if (response.code === 1) {
									this.$emit('submit', { //这里是传出去给调用者的，参数任意下，只要能区分就行
										type: 'reset',
										value: this.paymentPwd
									});
									this.modalFun('hide');
								}
							}, 1000)
						} else { //修改支付密码
							uni.showLoading({
								title: '正在重置密码'
							});
							// 如果不把逻辑卸载这里面，直接把setTimeout删除即可
							setTimeout(() => { //模拟请求，把这个换成ajax请求哈，或者emit出去 外面去请求，但是建议都集成在这边 省的写一堆重置代码
								uni.hideLoading();
								let response = { //模拟返回
									code: 1,
									msg: '重置新支付密码成功'
								}
								if (response.code === 1) {
									this.$emit('submit', {
										type: 'modify',
										value: this.paymentPwd
									});
									this.modalFun('hide');
								}
							}, 1000)
						}
						return;
					}
					this.checkSafePwd('check');
				} else {
					if (this._mode === 3 && this.current === 0) { //校验支付密码正确性
						this.checkSafePwd('verify');
						return;
					}
					this.changeSwiper(1);
				}
			}
		},
		checkSafePwd(type) {
			this.$emit('submit', { //设置或者重置新支付密码
				type: 'check',
				value: this.paymentPwd
			});
		},
		changeSwiper(num) {
			if (isNaN(num)) {
				num = 1;
			}
			this.current += num;
			this.paymentPwd = this.paymentPwds[this.current] || '';
			let activeInput = this.paymentPwd.length
			this.activeInput = activeInput - (activeInput >= this._digits ? 1 : 0);
		},
		getKeyNumber(val) {
			let paymentPwdArr = this.paymentPwd.split('');
			if (val === '' || (val != -1 && paymentPwdArr.length === this._digits)) { //空或者已经达到最大值
				if (paymentPwdArr.length === this._digits && this.trigger === 'auto') {
					this.submit();
					return;
				}
				return false;
			} else if (val != -1) { //数字输入
				paymentPwdArr.splice(this.activeInput, 0, val);
				this.activeInput++;
				this.paymentPwd = paymentPwdArr.join('');
				this.paymentPwds[this.current] = this.paymentPwd;
				if (paymentPwdArr.length === this._digits && this.trigger === 'auto') {
					this.submit();
					return;
				}
			} else { //删除
				if (this.activeInput >= 0) {
					this.activeInput !== 0 && this.activeInput--;
					paymentPwdArr.splice(this.activeInput, 1);
					this.paymentPwd = paymentPwdArr.join('');
					this.paymentPwds[this.current] = this.paymentPwd;
				}
			}
		},
		getKeyboard(e) {
			let index = e && e.target.dataset.index;
			if (index === undefined) {
				this.activeInput = 0;
				this.changeKeyboard(false);
				return;
			}
			var _length = this.paymentPwd.length;
			this.activeInput = index <= _length ? index : _length;
			this.changeKeyboard();
		}
	}
}
